import { _decorator, Component, Node, SpriteFrame, Sprite, resources, Prefab, instantiate } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('sgj_sprite')
export class sgj_sprite extends Component {

    //赢分、我的积分、比倍 用的数字
    @property(SpriteFrame)
    up_number_spf: SpriteFrame[] = [];

    //下注用的数字
    @property(SpriteFrame)
    dowm_number_spf: SpriteFrame[] = [];

    //赢分spr
    @property(Sprite)
    win_score_spr: Sprite[] = [];

    //我的积分spr
    @property(Sprite)
    user_score_spr: Sprite[] = [];

    //提示框
    @property(Node)
    tips_node: Node;

    static instance = null;

    public static getInstance() {
        if (sgj_sprite.instance == null) {
            sgj_sprite.instance = new sgj_sprite();
            return sgj_sprite.instance;
        }
        else {
            return sgj_sprite.instance;
        }
    }

    //设置赢分
    setWinScore(score: number) {
        let str = score.toString();
        let len = str.length;
        for (let i = 0; i < 10; i++) {
            if (i < len) {
                let st = Number(str[len - i - 1]);
                this.win_score_spr[i].spriteFrame = this.up_number_spf[st];
                this.win_score_spr[i].node.active = true;
            }
            else {
                this.win_score_spr[i].node.active = false;
            }
        }
    }

    //设置用户分数
    setUserScore(score: number) {
        let str = score.toString();
        let len = str.length;
        for (let i = 0; i < 10; i++) {
            if (i < len) {
                let st = Number(str[len - i - 1]);
                sgj_sprite.getInstance().user_score_spr[i].spriteFrame = sgj_sprite.getInstance().up_number_spf[st];
                sgj_sprite.getInstance().user_score_spr[i].node.active = true;
            }
            else {
                sgj_sprite.getInstance().user_score_spr[i].node.active = false;
            }
        }
    }

    //arg1:弹窗内容
    addTops(arg1: string) {
        resources.load("prefab/tips", Prefab, (err, prefab) => {
            let t_fb = instantiate(prefab);
            this.tips_node.addChild(t_fb);
            let t_script = t_fb.getComponent("tips");
            t_script.onShow(arg1);
        });
    }

    start() {

    }

    update(deltaTime: number) {

    }
}

